import React from 'react';
import { Tabs, Tooltip, Zoom, Tab, SvgIcon } from '@material-ui/core';
import { Link as RouterLink } from 'react-router-dom';

const HorizontalNavItems = (props) => {
  const {
    horizontalNavItemList,
    horizontalNavItemValue,
    horizontalTabsValueHandler,
  } = props;
  return (
    <Tabs
      centered
      value={horizontalNavItemValue}
      onChange={horizontalTabsValueHandler}
      textColor="secondary"
      aria-label="icon label tabs"
    >
      {horizontalNavItemList.map((linkItem) => {
        return (
          <Tooltip
            key={`${linkItem.label}`}
            arrow
            TransitionComponent={Zoom}
            title={linkItem.name}
            value={`/${linkItem.label}`}
          >
            <Tab
              component={RouterLink}
              to={`/${linkItem.linkTo}`}
              icon={
                <SvgIcon>
                  <path d={`${linkItem.svgPath}`} />
                </SvgIcon>
              }
            />
          </Tooltip>
        );
      })}
    </Tabs>
  );
};

export default HorizontalNavItems;
