import React from 'react';
import { Switch, Route } from 'react-router-dom';

import NavBar from './components/core/NavBar';
import Home from './components/core/Home';
import LatestCard from './components/Card/LatestCard';
import Details from './components/Account/Details';
import CardDetails from './components/Account/CardDetails';
import SignIn from './components/Account/SignIn';
import SignUp from './components/Account/SignUp';
import CardTabs from './components/Account/CardTabs';
import PrivateRoute from './components/Auth/PrivateRoute';

const MainRouter = () => {
  return (
    <>
      <NavBar />
      <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/latest" component={LatestCard} />
        <Route exact path="/features">
          <p>Features</p>
        </Route>
        <Route exact path="/regions">
          <p>Regions</p>
        </Route>
        <PrivateRoute exact path="/account" component={Details} />
        <PrivateRoute exact path="/card" component={CardTabs} />
        <Route exact path="/signin" component={SignIn} />
        <Route exact path="/signup" component={SignUp} />
      </Switch>
    </>
  );
};

export default MainRouter;
