import React from 'react';
import {
  ListItem,
  ListItemText,
  Link,
  Avatar,
  SvgIcon,
  ListItemAvatar,
  ListItemIcon,
  useMediaQuery,
} from '@material-ui/core';
import { useTheme } from '@material-ui/styles';
import { Link as RouterLink } from 'react-router-dom';

const AccountOption = (props) => {
  const theme = useTheme();
  const isSmallDevice = useMediaQuery(theme.breakpoints.down('sm'));

  return (
    <ListItem
      button
      component={RouterLink}
      to={`/${props.linkItem.linkTo}`}
      divider
      dense={!isSmallDevice}
      onClick={props.optionsHandler}
    >
      <ListItemIcon>
        {/* <Avatar>{actionLink(props.linkItem.label)}</Avatar> */}
        <SvgIcon>
          <path d={props.linkItem.svgPath} />
        </SvgIcon>
      </ListItemIcon>
      <ListItemText primary={props.linkItem.name} />
    </ListItem>
  );
};

export default AccountOption;
