import React from 'react';
import {
  Grid,
  Divider,
  useMediaQuery,
  List,
  ListSubheader,
  ListItem,
  ListItemIcon,
  SvgIcon,
  ListItemText,
} from '@material-ui/core';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { Link as RouterLink } from 'react-router-dom';

import AccountInfo from './AccountInfo';
import VerticalNavItems from './VerticalNavItems';
import { useStore } from '../../hooks/store';
import SignOut from './SignOut';
import { useWindowSize } from '../../hooks/useWindowSize';

const useStyles = makeStyles((theme) => ({
  drawerContainer: {
    [theme.breakpoints.down('sm')]: {
      width: 'auto',
    },
    [theme.breakpoints.up('sm')]: { width: 256 },
  },
  drawerHeader: { height: 150 },
  headerItems: { margin: `0 ${theme.spacing(2)}` },
  headerItem: { margin: `${theme.spacing(3)} 0` },
}));

const DrawerContent = (props) => {
  const {
    toggleDrawer,
    accountItemList,
    tabsValueHandler,
    verticalNavItemList,
    verticalNavItemValue,
    isActive,
    history,
  } = props;

  const classes = useStyles();
  const theme = useTheme();
  const { width, height } = useWindowSize();
  const isSmallDevice = useMediaQuery(theme.breakpoints.down('sm'));
  const [state, dispatch] = useStore();

  return (
    <Grid
      container
      direction="column"
      className={classes.drawerContainer}
      style={{ height: isSmallDevice ? height / 2 : height }}
      wrap="nowrap"
    >
      <Grid item>
        <Grid
          container
          direction="column"
          wrap="nowrap"
          justifyContent="center"
          alignItems="flex-start"
          className={classes.drawerHeader}
        >
          <AccountInfo direction="column" />
        </Grid>
      </Grid>
      <Divider />
      <List>
        {verticalNavItemList.length > 0 ? (
          <Grid item>
            <VerticalNavItems
              accountItemList={accountItemList}
              verticalNavItemList={verticalNavItemList}
              verticalNavItemValue={verticalNavItemValue}
              toggleDrawer={toggleDrawer}
              isActiveStyle={isActive}
              routeHistory={history}
            />
            <Divider />
          </Grid>
        ) : null}
        {isSmallDevice ? (
          <>
            <ListSubheader>Account</ListSubheader>
            {accountItemList.map((accountItem) => (
              <ListItem
                button
                key={`${accountItem.label}`}
                selected={history === `/${accountItem.linkTo}`}
                component={RouterLink}
                to={`/${accountItem.linkTo}`}
                style={isActive(history, `/${accountItem.linkTo}`)}
                onClick={toggleDrawer}
                dense={!isSmallDevice}
              >
                <ListItemIcon>
                  <SvgIcon style={isActive(history, `/${accountItem.linkTo}`)}>
                    <path d={accountItem.svgPath} />
                  </SvgIcon>
                </ListItemIcon>
                <ListItemText primary={accountItem.name} />
              </ListItem>
            ))}
            {state.isAuthenticated ? (
              <SignOut onClickHandler={toggleDrawer} />
            ) : null}
            <Divider />
          </>
        ) : null}
      </List>
    </Grid>
  );
};

export default DrawerContent;
