import React, { useRef, useState, useEffect } from 'react';
import {
  Tab,
  Tooltip,
  SvgIcon,
  Zoom,
  useMediaQuery,
  Paper,
  Typography,
  Grid,
} from '@material-ui/core';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { LoadingButton, TabList, TabPanel, TabContext } from '@material-ui/lab';

import { useWindowSize } from '../../hooks/useWindowSize';
import {
  linkIcon,
  linkLockIcon,
  earthIcon,
  pencilIcon,
  trashIcon,
  addIcon,
} from '../../assets/svg/SvgIcons';
import PaperContainer from '../../HOC/PaperContainer';
import CardList from '../Card/CardList';
import { FullList } from '../Demo/FullList';

const useStyles = makeStyles((theme) => ({
  tabContainer: { padding: `${theme.spacing(3)} 0` },
}));

const CardTabs = (props) => {
  const { width, height } = useWindowSize();
  const theme = useTheme();
  const isSmallDevice = useMediaQuery(theme.breakpoints.down('sm'));
  const isMediumDevice = useMediaQuery(theme.breakpoints.between('sm', 'lg'));
  const isLargeDevice = useMediaQuery(theme.breakpoints.up('lg'));
  const classes = useStyles();
  const initCardTypeList = [
    { name: 'Public', type: 'public', svgPath: earthIcon.path },
    { name: 'Unlisted', type: 'unlisted', svgPath: linkIcon.path },
    { name: 'Private', type: 'private', svgPath: linkLockIcon.path },
  ];
  const [cardTypeList, setCardTypeList] = useState(initCardTypeList);
  const [publicCardData, setPublicCardData] = useState(FullList);
  const [unlistedCardData, setUnlistedCardData] = useState(FullList);
  const [privateCardData, setPrivateCardData] = useState(FullList);
  const [activeTab, setActiveTab] = useState('public');
  const [isPendingPrev, setIsPendingPrev] = useState(false);
  const [isPendingNext, setIsPendingNext] = useState(false);
  const [nameCardList, setNameCardList] = useState([
    {
      id: 1,
      text: '1',
      image:
        'https://images.unsplash.com/photo-1487017931017-0e0d9e02bb0c?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1350&q=80',
      backgroundColor: '#265574',
    },
    {
      id: 2,
      text: '2',
      image:
        'https://mages.unsplash.com/photo-1573502463904-4eaf674fd668?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=564&q=80',
      backgroundColor: '#424290',
    },
    {
      id: 3,
      text: '3',
      image:
        'https://images.unsplash.com/uploads/1412026095116d2b0c90e/3bf33993?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1347&q=80',
      backgroundColor: '#00d4ff',
    },
    {
      id: 4,
      text: '4',
      image:
        'https://images.unsplash.com/photo-1536147116438-62679a5e01f2?ixlib=rb-1.2.1&ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&auto=format&fit=crop&w=634&q=80',
      backgroundColor: '#9a9d4e',
    },
    {
      id: 5,
      text: '5',
      background: 'blue',
      image:
        'https://images.unsplash.com/photo-1561016444-14f747499547?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=2089&q=80',
      backgroundColor: '#fc466b',
    },
  ]);

  const tabChangeHandler = (event, newValue) => {
    setActiveTab(newValue);
  };

  return (
    <PaperContainer hasGutter={false}>
      <TabContext value={activeTab}>
        <TabList
          centered={isSmallDevice ? false : true}
          variant={isSmallDevice ? 'fullWidth' : 'standard'}
          onChange={tabChangeHandler}
        >
          {cardTypeList.map((cardType) => (
            <Tooltip
              key={cardType.type}
              arrow
              TransitionComponent={Zoom}
              title={cardType.name}
              value={cardType.type}
            >
              <Tab
                icon={
                  <SvgIcon>
                    <path d={cardType.svgPath} />
                  </SvgIcon>
                }
              />
            </Tooltip>
          ))}
        </TabList>

        <TabPanel className={classes.tabContainer} value={'public'}>
          <Paper
            style={{
              width: isSmallDevice
                ? width
                : isMediumDevice
                ? theme.breakpoints.values.xs
                : isLargeDevice
                ? theme.breakpoints.values.sm
                : theme.breakpoints.values.xxs,
            }}
          >
            <Grid container direction="column">
              <Grid
                item
                container
                justifyContent="space-between"
                className={classes.upperTabPanel}
              >
                <Grid item>
                  <LoadingButton
                    startIcon={
                      <SvgIcon>
                        <path d={addIcon.path} />
                      </SvgIcon>
                    }
                    color="secondary"
                    pendingPosition="start"
                    variant="contained"
                    className={classes.buttonSpacing}
                    type="submit"
                  >
                    Create
                  </LoadingButton>
                </Grid>

                <Grid
                  item
                  style={{
                    width: isSmallDevice
                      ? width
                      : isMediumDevice
                      ? theme.breakpoints.values.xs
                      : isLargeDevice
                      ? theme.breakpoints.values.sm
                      : theme.breakpoints.values.xxs,
                  }}
                >
                  <CardList cardDataList={publicCardData} />
                </Grid>
              </Grid>
            </Grid>
          </Paper>
        </TabPanel>

        <TabPanel value={'unlisted'}>
          <Typography>Unlisted</Typography>
        </TabPanel>
        <TabPanel value={'private'}>
          <Typography>Private</Typography>
        </TabPanel>
      </TabContext>
    </PaperContainer>
  );
};

export default CardTabs;
