import { initStore } from './store';

const configureStore = () => {
  const actions = {
    SIGN_IN: (curState, user) => {
      const copiedUser = { ...curState.user, ...user.user };
      return { user: copiedUser, isAuthenticated: true };
    },

    SIGN_OUT: (curState) => {
      const resetUser = {
        ...curState.user,
        name: '',
        email: '',
        image: '',
        birthDate: '',
        password: '',
        accountType: '',
      };
      console.log(resetUser);
      return { user: resetUser, isAuthenticated: false };
    },
  };

  initStore(actions, {
    user: {
      name: '',
      email: '',
      image: '',
      birthDate: '',
      password: '',
      accountType: '',
    },
    isAuthenticated: false,
  });
};

export default configureStore;
