import React, { useState } from 'react';
import { Grid, Avatar, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';

import { useStore } from '../../hooks/store';

const useStyles = makeStyles((theme) => ({
  headerContainer: {
    height: 100,
  },
  horizontalMargin: { margin: `0 ${theme.spacing(2)}` },
  verticalMargin: { margin: `${theme.spacing(1)} 0` },
}));

const AccountInfo = (props) => {
  const [state, dispatch] = useStore(true);
  const { direction } = props;

  const classes = useStyles();

  return (
    <Grid item>
      <Grid
        container
        direction={direction}
        justifyContent={direction === 'row' ? 'flex-start' : 'center'}
        alignItems={direction === 'row' ? 'center' : 'flex-start'}
        wrap="nowrap"
        className={clsx({
          [classes.headerContainer]: direction === 'row',
          [classes.horizontalMargin]: direction === 'column',
        })}
      >
        <Grid
          item
          className={clsx({
            [classes.horizontalMargin]: direction === 'row',
            [classes.verticalMargin]: direction === 'column',
          })}
        >
          <Avatar src={state.isAuthenticated ? state.user.image : null} />
        </Grid>
        <Grid
          item
          className={clsx({ [classes.verticalMargin]: direction === 'column' })}
        >
          <Grid item>
            <Typography variant="subtitle1">
              {state.isAuthenticated ? state.user.name : 'Guest'}
            </Typography>
          </Grid>
          <Grid item>
            <Typography variant="subtitle2">
              {state.isAuthenticated ? state.user.email : 'Not Sign In'}
            </Typography>
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  );
};

export default AccountInfo;
