import React from 'react';
import { Paper, Grid, isWidthDown } from '@material-ui/core';
import { useTheme, makeStyles } from '@material-ui/core/styles';

import { useWindowSize } from '../hooks/useWindowSize';

const useStyles = makeStyles((theme) => ({
  inputContainer: {
    minWidth: theme.breakpoints.values.xxs,
    [theme.breakpoints.down('sm')]: {
      width: (width) => width,
      borderRadius: theme.spacing(0),
      padding: `${theme.spacing(3)} 0`,
    },
    [theme.breakpoints.between('sm', 'lg')]: {
      width: theme.breakpoints.values.sm,
    },
    [theme.breakpoints.up('lg')]: {
      width: theme.breakpoints.values.smd,
    },
    [theme.breakpoints.up('sm')]: {
      padding: (width, hasGutter) =>
        hasGutter ? theme.spacing(3) : `${theme.spacing(3)} 0`,
      marginTop: theme.spacing(3),
    },
  },
  buttonSpacing: {
    marginTop: theme.spacing(1),
  },
}));

const PaperContainer = (props) => {
  const { children, hasGutter } = props;
  const { width, height } = useWindowSize();
  const classes = useStyles(width, hasGutter);

  return (
    <Grid container direction="row" justifyContent="center" alignItems="center">
      <Paper className={classes.inputContainer}>{children}</Paper>
    </Grid>
  );
};

export default PaperContainer;
