import React, { useState, useRef, useEffect, useCallback } from 'react';
import { Grid, Avatar, SvgIcon } from '@material-ui/core';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { LoadingButton } from '@material-ui/lab';
import { Redirect } from 'react-router-dom';

import {
  closeIcon,
  visibilityOffIcon,
  visibilityOnIcon,
} from '../../assets/svg/SvgIcons';
import { useStore } from '../../hooks/store';
import RestructureFormObject from '../helper/RestructureFormObject';
import { fieldChangeHandler } from '../helper/fieldChangeHandler';
import { uploadIcon, pencilIcon, saveIcon } from '../../assets/svg/SvgIcons';
import PaperContainer from '../../HOC/PaperContainer';

const useStyles = makeStyles((theme) => ({
  avatarIcon: {
    width: theme.spacing(24),
    height: theme.spacing(24),
    border: '1px red solid',
  },
  contentItem: {
    margin: theme.spacing(1),
  },
}));

const Details = (props) => {
  const [state, dispatch] = useStore(true);
  const [user, setUser] = useState({
    email: {
      value: state.user.email,
      name: 'email',
      label: 'Email',
      type: 'email',
      placeHolder: '',
      multiline: false,
      disabled: true,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      validity: { isValid: true, label: '*Required' },
      icon: {
        type: 'close',
        close: closeIcon,
      },
    },
    password: {
      value: state.user.password,
      name: 'password',
      label: 'Password',
      type: 'password',
      placeHolder: '',
      multiline: false,
      disabled: true,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      validity: { isValid: true, label: '*Required' },
      icon: {
        type: 'toggle',
        on: visibilityOnIcon,
        off: visibilityOffIcon,
        isOn: false,
      },
    },
    name: {
      value: state.user.name,
      name: 'name',
      label: 'Name',
      type: 'text',
      placeHolder: '',
      multiline: false,
      disabled: true,
      validations: {
        isRequired: true,
      },
      isTouched: false,
      validity: { isValid: true, label: '*Required' },
      icon: {
        type: 'close',
        close: closeIcon,
      },
    },
    // image: {
    //   value: '',
    //   name: 'image',
    //   label: 'Image',
    //   type: 'text',
    //   placeHolder: '',
    //   multiline: false,
    //   validations: {
    //     isRequired: true,
    //   },
    //   isTouched: false,
    //   validity: { isValid: false, label: '*Required' },
    // },
  });
  const [profilePicture, setProfilePicture] = useState();
  const [profileIsLoading, setProfileIsLoading] = useState(false);
  const [previewProfile, setPreviewProfile] = useState();
  const [profileIsValid, setProfileIsValid] = useState(true);
  const [profileButtonIsEnabled, setProfileButtonIsEnabled] = useState(false);
  const [profileState, setProfileState] = useState('read');
  const [profileFormIsValid, setProfileFormIsValid] = useState(true);

  const classes = useStyles();

  useEffect(() => {
    if (!profilePicture) {
      return;
    }
    const fileReader = new FileReader();
    fileReader.onload = () => {
      setPreviewProfile(fileReader.result);
    };
    fileReader.readAsDataURL(profilePicture);

    // setProfileIsLoading(false);
  }, [profilePicture]);

  const profileRef = useRef();

  const profileFormCompleteChangeHandler = useCallback((args) => {
    const { formObject } = { ...args };

    const updatedForm = { ...formObject };

    const formValidityList = [];

    for (let key in updatedForm) {
      formValidityList.push(updatedForm[key].validity.isValid);
    }

    let formIsValid = true;
    formValidityList.map((isValid) => (formIsValid = formIsValid && isValid));

    setProfileFormIsValid(formIsValid);
  }, []);

  useEffect(() => {
    profileFormCompleteChangeHandler({ formObject: user });
  }, [user, profileFormCompleteChangeHandler]);

  const userDetailsChangeHandler = (args) => (event) => {
    const updatedForm = fieldChangeHandler(args, event);

    setUser(updatedForm);
  };

  const profileChangeHandler = (event) => {
    let pickedProfile;

    if (event.target.files && event.target.files.length === 1) {
      pickedProfile = event.target.files[0];
      setProfilePicture(pickedProfile);
      setProfileIsValid(true);
    } else {
      setProfileIsValid(false);
    }
  };

  const pickProfileHandler = () => {
    // setProfileIsLoading(true);
    profileRef.current.click();
  };

  const editProfileHandler = () => {
    profileState === 'read'
      ? setProfileState('update')
      : setProfileState('read');

    setProfileButtonIsEnabled(!profileButtonIsEnabled);

    const copiedUser = JSON.parse(JSON.stringify(user));

    for (const key in copiedUser) {
      copiedUser[key].disabled = !copiedUser[key].disabled;
    }

    setUser(copiedUser);
  };

  if (!state.isAuthenticated) {
    return <Redirect to="/signin" />;
  }

  return (
    <PaperContainer>
      <Grid
        container
        direction="column"
        justifyContent="flex-start"
        alignItems="center"
      >
        <Grid item>
          <input
            id="profile-picture"
            ref={profileRef}
            style={{ display: 'none' }}
            type="file"
            accept=".jpg,.png,.jpeg"
            onChange={profileChangeHandler}
          />
        </Grid>
        <Grid item className={classes.contentItem}>
          <Avatar className={classes.avatarIcon} src={state.user.image} />
        </Grid>
        <Grid item className={classes.contentItem}>
          <LoadingButton
            startIcon={
              <SvgIcon>
                <path d={uploadIcon.path} />
              </SvgIcon>
            }
            disabled={!profileButtonIsEnabled}
            color="secondary"
            onClick={pickProfileHandler}
            pendingPosition="start"
            variant="outlined"
            pending={profileIsLoading}
          >
            Upload Profile
          </LoadingButton>
        </Grid>
        <RestructureFormObject
          formObject={user}
          formChangeHandler={userDetailsChangeHandler}
        />
        <Grid item className={classes.contentItem}>
          {profileState === 'read' ? (
            <LoadingButton
              startIcon={
                <SvgIcon>
                  <path d={pencilIcon.path} />
                </SvgIcon>
              }
              color="secondary"
              onClick={editProfileHandler}
              pendingPosition="start"
              variant="contained"
              // pending={profileIsLoading}
            >
              Edit
            </LoadingButton>
          ) : (
            <LoadingButton
              startIcon={
                <SvgIcon>
                  <path d={saveIcon.path} />
                </SvgIcon>
              }
              disabled={!profileFormIsValid}
              color="secondary"
              onClick={editProfileHandler}
              pendingPosition="start"
              variant="contained"
              // pending={profileIsLoading}
            >
              Save
            </LoadingButton>
          )}
        </Grid>
      </Grid>
    </PaperContainer>
  );
};

export default Details;
