import React from 'react';
import { List } from '@material-ui/core';

import AccountDropDownOption from './AccountDropDownOption';
import { useStore } from '../../hooks/store';
import SignOut from './SignOut';

const AccountDropDownOptions = (props) => {
  const { optionsHandler, options } = props;

  const [state, dispatch] = useStore(true);

  return (
    <List>
      {options.map((linkItem, index) => (
        <AccountDropDownOption
          key={Math.random()}
          linkItem={linkItem}
          optionsHandler={optionsHandler}
        />
      ))}
      {state.isAuthenticated ? (
        <SignOut onClickHandler={optionsHandler} />
      ) : null}
    </List>
  );
};

export default AccountDropDownOptions;
