import React from 'react';
import {
  ListItem,
  ListItemText,
  Link,
  Avatar,
  SvgIcon,
  ListItemAvatar,
  useMediaQuery,
} from '@material-ui/core';
import { useTheme } from '@material-ui/styles';

const CardMoreOption = (props) => {
  const theme = useTheme();
  const isSmallDevice = useMediaQuery(theme.breakpoints.down('sm'));

  return (
    <li key={props.linkItem.title}>
      <Link
        color="inherit"
        underline="none"
        target="_blank"
        rel="noopener"
        component="a"
        onClick={props.moreOptionsHandler}
      >
        <ListItem button divider dense={!isSmallDevice}>
          <ListItemAvatar>
            {/* <Avatar>{actionLink(props.linkItem.label)}</Avatar> */}
            <SvgIcon>
              <path d={props.linkItem.svgPath} />
            </SvgIcon>
          </ListItemAvatar>
          <ListItemText
            primary={props.linkItem.title}
            secondary={props.linkItem.description}
          />
        </ListItem>
      </Link>
    </li>
  );
};

export default CardMoreOption;
