import React from 'react';
import { Grid, SvgIcon, IconButton, useMediaQuery } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import LoadingButton from '@material-ui/lab/LoadingButton';

import { trashIcon } from '../../assets/svg/SvgIcons';
import RestructureFormObject from './RestructureFormObject';

const useStyles = makeStyles((theme) => ({
  iconButton: {
    marginTop: theme.spacing(2.5),
  },
}));

const FormObjectList = (props) => {
  const classes = useStyles();
  const isLargeDevice = useMediaQuery((theme) => theme.breakpoints.up('lg'));

  const { objectList, formChangeHandler, removeFormObjectHandler, flowType } = {
    ...props,
  };

  return objectList.map((formObject, index) => (
    <Grid
      key={index}
      container
      direction={isLargeDevice && flowType === 'flex' ? 'row' : 'column'}
      justifyContent="center"
      alignItems={isLargeDevice && flowType === 'flex' ? null : 'center'}
    >
      <RestructureFormObject
        formObject={formObject}
        formChangeHandler={formChangeHandler}
        objectList={objectList}
        index={index}
      />
      <Grid item>
        {isLargeDevice && flowType === 'flex' ? (
          <IconButton
            className={classes.iconButton}
            aria-label="delete"
            color="secondary"
            onClick={removeFormObjectHandler({
              objectList,
              index,
            })}
          >
            <SvgIcon>
              <path d={trashIcon.path} />
            </SvgIcon>
          </IconButton>
        ) : (
          <LoadingButton
            aria-label="delete"
            color="secondary"
            onClick={removeFormObjectHandler({
              objectList,
              index,
            })}
            startIcon={
              <SvgIcon>
                <path d={trashIcon.path} />
              </SvgIcon>
            }
            pendingPosition="start"
            variant="outlined"
          >
            DELETE
          </LoadingButton>
        )}
      </Grid>
    </Grid>
  ));
};

export default FormObjectList;
