import React, { useState } from 'react';
import { Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

import CardMain from '../Card/CardMain';
import { SingleItem } from '../Demo/SingleItem';

const useStyles = makeStyles((theme) => ({
  cardContainer: {
    [theme.breakpoints.up('sm')]: {
      marginTop: theme.spacing(3),
    },
  },
}));

const Home = (props) => {
  const [cardData, setCardData] = useState(SingleItem);
  const [cardIsExpanded, setCardIsExpanded] = useState(true);

  const classes = useStyles();

  return (
    <Grid
      container
      direction="column"
      justify="flex-start"
      alignItems="center"
      className={classes.cardContainer}
    >
      <Grid item>
        <CardMain cardData={cardData} cardIsExpanded={cardIsExpanded} />
      </Grid>
    </Grid>
  );
};

export default Home;
