import React from 'react';
import InputTextRenderer from './InputTextRenderer';

const RestructureFormObject = (props) => {
  const { formObject, formChangeHandler, objectList = '', index = 0 } = {
    ...props,
  };

  const formList = [];

  for (let key in formObject) {
    formList.push({
      id: `${formObject[key].name}-${index}`,
      fieldName: key,
      config: formObject[key],
      formObject,
      formChangeHandler,
      objectList,
      index,
    });
  }

  return <InputTextRenderer formList={formList} />;
};

export default RestructureFormObject;
