import React, { useCallback, useMemo, useState, useEffect } from 'react';
import {
  AppBar,
  Toolbar,
  IconButton,
  SvgIcon,
  Grid,
  Tooltip,
  Zoom,
  useMediaQuery,
} from '@material-ui/core';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { withRouter } from 'react-router-dom';
import { useStore } from '../../hooks/store';

import {
  menuIcon,
  searchIcon,
  backIcon,
  emailSendIcon,
  loginIcon,
  accountBoxIcon,
  accountCardIcon,
  newIcon,
  cardStarIcon,
  earthIcon,
} from '../../assets/svg/SvgIcons';
import SearchBar from './SearchBar';
import AccountDropDown from './AccountDropDown';
import Logo from './Logo';
import HorizontalNavItems from './HorizontalNavItems';
import DrawerContainer from './DrawerContainer';

const useStyles = makeStyles((theme) => ({
  offset: theme.mixins.toolbar,
}));

const NavBar = withRouter(({ history, location }, props) => {
  const initLinkList = [
    {
      name: 'Latest',
      label: 'latest',
      linkTo: 'latest',
      svgPath: newIcon.path,
      type: 'public',
    },
    {
      name: 'Featured',
      label: 'featured',
      linkTo: 'featured',
      svgPath: cardStarIcon.path,
      type: 'public',
    },
    {
      name: 'Regions',
      label: 'regions',
      linkTo: 'regions',
      svgPath: earthIcon.path,
      type: 'public',
    },
    {
      name: 'Test 1',
      label: 'test1',
      linkTo: 'test1',
      svgPath: earthIcon.path,
      type: 'public',
    },
    {
      name: 'Test 2',
      label: 'test2',
      linkTo: 'test2',
      svgPath: earthIcon.path,
      type: 'public',
    },
    {
      name: 'Account Details',
      label: 'accountDetails',
      linkTo: 'account',
      svgPath: accountBoxIcon.path,
      type: 'authenticated',
    },
    {
      name: 'Create Card',
      label: 'createCard',
      linkTo: 'card',
      svgPath: accountCardIcon.path,
      type: 'authenticated',
    },
    {
      name: 'Sign Up',
      label: 'signup',
      linkTo: 'signup',
      svgPath: emailSendIcon.path,
      type: 'unauthenticated',
    },
    {
      name: 'Sign In',
      label: 'signin',
      linkTo: 'signin',
      svgPath: loginIcon.path,
      type: 'unauthenticated',
    },
  ];
  const theme = useTheme();
  const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');
  const isSmallDevice = useMediaQuery(theme.breakpoints.down('sm'));
  const isSmallMediumDevice = useMediaQuery(
    theme.breakpoints.between('sm', 'smd')
  );
  const isMediumDevice = useMediaQuery(theme.breakpoints.between('smd', 'md'));
  const isLargeMediumDevice = useMediaQuery(
    theme.breakpoints.between('md', 'lmd')
  );
  const isLargeDevice = useMediaQuery(theme.breakpoints.between('lmd', 'lg'));
  const isExtraLargeDevice = useMediaQuery(theme.breakpoints.up('lg'));

  const classes = useStyles();

  const [state, dispatch] = useStore(true);

  const [drawerIsOpen, setDrawerIsOpen] = useState(false);
  const [linkItemList, setLinkItemList] = useState(initLinkList);

  const [horizontalNavItemList, setHorizontalNavItemList] = useState([]);
  const [horizontalNavItemValue, setHorizontalNavItemValue] = useState(false);

  const [verticalNavItemList, setVerticalNavItemList] = useState([]);
  const [verticalNavItemValue, setVerticalNavItemValue] = useState(false);

  const [navItemNumber, setNavItemNumber] = useState(0);

  const [showSearchBar, setShowSearchBar] = useState(false);

  const navItemNumberHandler = useCallback(() => {
    // console.log('setNavItemNumber');

    isSmallDevice && setNavItemNumber(0);
    isSmallMediumDevice && setNavItemNumber(1);
    isMediumDevice && setNavItemNumber(2);
    isLargeMediumDevice && setNavItemNumber(3);
    isLargeDevice && setNavItemNumber(4);
    isExtraLargeDevice && setNavItemNumber(5);
  }, [
    isSmallDevice,
    isSmallMediumDevice,
    isMediumDevice,
    isLargeMediumDevice,
    isLargeDevice,
    isExtraLargeDevice,
  ]);

  const navItemListHandler = useCallback(
    (navItems) => {
      if (linkItemList.length > 0) {
        const copiedLinkItemList = [...linkItemList];
        const navItemList = copiedLinkItemList.filter(
          (linkItem) => linkItem.type === 'public'
        );

        let verticalNavItems = [];
        let horizontalNavItems = [];
        if (navItems > 0) {
          horizontalNavItems = navItemList.splice(0, navItems);
          verticalNavItems = navItemList;
        } else {
          verticalNavItems = navItemList;
        }

        setHorizontalNavItemList(horizontalNavItems);
        setVerticalNavItemValue(false);
        setVerticalNavItemList(verticalNavItems);
        setHorizontalNavItemValue(false);
      }
    },
    [linkItemList]
  );

  const verticalTabsValueHandler = useCallback(
    (event, newValue) => {
      const tabsValue = verticalNavItemList.map(
        (linkItem) => `/${linkItem.label}`
      );

      const pathIsExistInTabs = tabsValue.includes(newValue);

      if (!pathIsExistInTabs) {
        setVerticalNavItemValue(false);
      } else {
        setHorizontalNavItemValue(false);
        setVerticalNavItemValue(newValue);
      }
    },
    [verticalNavItemList]
  );

  const horizontalTabsValueHandler = useCallback(
    (event, newValue) => {
      const tabsValue = horizontalNavItemList.map(
        (linkItem) => `/${linkItem.label}`
      );
      const pathIsExistInTabs = tabsValue.includes(newValue);

      if (!pathIsExistInTabs) {
        setHorizontalNavItemValue(false);
      } else {
        setVerticalNavItemValue(false);
        setHorizontalNavItemValue(newValue);
      }
    },
    [horizontalNavItemList]
  );

  const tabsValueHandler = useCallback(
    (event, newValue) => {
      verticalTabsValueHandler(event, newValue);
      horizontalTabsValueHandler(event, newValue);
    },
    [verticalTabsValueHandler, horizontalTabsValueHandler]
  );

  useEffect(() => {
    navItemListHandler(navItemNumber);
  }, [navItemNumber, navItemListHandler]);

  useEffect(() => {
    navItemNumberHandler();
  }, [navItemNumberHandler]);

  useEffect(() => {
    tabsValueHandler('', location.pathname);
  }, [location.pathname, tabsValueHandler]);

  const isActive = useMemo(
    () => (history, path) => {
      if (history.location.pathname === path) {
        return {
          color: prefersDarkMode
            ? theme.palette.secondary.dark
            : theme.palette.secondary.light,
        };
      }
    },
    [
      prefersDarkMode,
      theme.palette.secondary.dark,
      theme.palette.secondary.light,
    ]
  );

  const toggleDrawer = () => {
    setDrawerIsOpen((prev) => !prev);
  };

  const toggleSearchHandler = useCallback(() => {
    setShowSearchBar((prev) => !prev);
  }, []);

  return (
    <div>
      <AppBar position="fixed" color="default">
        <Toolbar variant={isSmallDevice ? 'regular' : 'dense'}>
          <Grid
            container
            direction="row"
            justifyContent="space-between"
            alignItems="center"
            wrap="nowrap"
          >
            <Grid item>
              {showSearchBar ? (
                <Grid
                  container
                  direction="row"
                  justifyContent="center"
                  alignItems="center"
                  wrap="nowrap"
                >
                  <Grid item>
                    <Tooltip TransitionComponent={Zoom} title="Back">
                      <IconButton onClick={toggleSearchHandler}>
                        <SvgIcon>
                          <path d={backIcon.path} />
                        </SvgIcon>
                      </IconButton>
                    </Tooltip>
                  </Grid>
                  <Grid item>
                    <SearchBar />
                  </Grid>
                </Grid>
              ) : (
                <Grid
                  container
                  direction="row"
                  justifyContent="center"
                  alignItems="center"
                  wrap="nowrap"
                >
                  {isSmallDevice ? null : (
                    <Grid item>
                      <Tooltip TransitionComponent={Zoom} title="More">
                        <IconButton
                          edge={false}
                          aria-label="menu"
                          onClick={toggleDrawer}
                        >
                          <SvgIcon>
                            <path d={menuIcon.path} />
                          </SvgIcon>
                        </IconButton>
                      </Tooltip>
                      <DrawerContainer
                        accountItemList={linkItemList.filter((link) => {
                          const accountItems = state.isAuthenticated
                            ? 'authenticated'
                            : 'unauthenticated';
                          return link.type === accountItems;
                        })}
                        toggleDrawer={toggleDrawer}
                        drawerIsOpen={drawerIsOpen}
                        tabsValueHandler={tabsValueHandler}
                        verticalNavItemList={verticalNavItemList}
                        verticalNavItemValue={verticalNavItemValue}
                        isActive={isActive}
                        history={history}
                      />
                    </Grid>
                  )}

                  <Grid item>
                    <Logo
                      isActiveStyle={isActive}
                      routeHistory={history}
                      tabsValueHandler={tabsValueHandler}
                    />
                  </Grid>
                  <Grid item>
                    <Tooltip TransitionComponent={Zoom} title="Search">
                      <IconButton onClick={toggleSearchHandler}>
                        <SvgIcon>
                          <path d={searchIcon.path} />
                        </SvgIcon>
                      </IconButton>
                    </Tooltip>
                  </Grid>
                </Grid>
              )}
            </Grid>

            <Grid item>
              {horizontalNavItemList ? (
                <HorizontalNavItems
                  horizontalNavItemList={horizontalNavItemList}
                  horizontalNavItemValue={horizontalNavItemValue}
                  horizontalTabsValueHandler={horizontalTabsValueHandler}
                  isActiveStyle={isActive}
                  routeHistory={history}
                />
              ) : null}
            </Grid>

            <Grid item>
              <Grid container direction="row" wrap="nowrap">
                <AccountDropDown
                  accountItemList={linkItemList.filter((link) => {
                    const accountItems = state.isAuthenticated
                      ? 'authenticated'
                      : 'unauthenticated';
                    return link.type === accountItems;
                  })}
                  toggleDrawer={toggleDrawer}
                  drawerIsOpen={drawerIsOpen}
                  tabsValueHandler={tabsValueHandler}
                  verticalNavItemList={verticalNavItemList}
                  verticalNavItemValue={verticalNavItemValue}
                  isActive={isActive}
                  history={history}
                />
              </Grid>
            </Grid>
          </Grid>
        </Toolbar>
      </AppBar>
      <div className={classes.offset} />
    </div>
  );
});

export default NavBar;
