import React from 'react';
import {
  ListItem,
  ListItemText,
  ListItemIcon,
  Link,
  SvgIcon,
} from '@material-ui/core';

import {
  linkIcon,
  facebookIcon,
  youtubeIcon,
  linkedInIcon,
  whatsAppIcon,
  instagramIcon,
  twitchIcon,
  vkIcon,
  twitterIcon,
} from '../../assets/svg/SvgIcons';

const CardLink = (props) => {
  const actionLinkIcon = (link) => {
    let icon = (
      <SvgIcon>
        <path d={linkIcon.path} />
      </SvgIcon>
    );

    if (link.startsWith('https://www.facebook.com')) {
      icon = (
        <SvgIcon>
          <path d={facebookIcon.path} />
        </SvgIcon>
      );
    } else if (link.startsWith('https://www.youtube.com')) {
      icon = (
        <SvgIcon>
          <path d={youtubeIcon.path} />
        </SvgIcon>
      );
    } else if (link.startsWith('https://www.linkedin.com')) {
      icon = (
        <SvgIcon>
          <path d={linkedInIcon.path} />
        </SvgIcon>
      );
    } else if (link.startsWith('https://www.whatsapp.com')) {
      icon = (
        <SvgIcon>
          <path d={whatsAppIcon.path} />
        </SvgIcon>
      );
    } else if (link.startsWith('https://www.instagram.com')) {
      icon = (
        <SvgIcon>
          <path d={instagramIcon.path} />
        </SvgIcon>
      );
    } else if (link.startsWith('https://www.twitch.tv')) {
      icon = (
        <SvgIcon>
          <path d={twitchIcon.path} />
        </SvgIcon>
      );
    } else if (link.startsWith('https://twitter.com')) {
      icon = (
        <SvgIcon>
          <path d={twitterIcon.path} />
        </SvgIcon>
      );
    } else if (link.startsWith('https://vk.com')) {
      icon = (
        <SvgIcon>
          <path d={vkIcon.path} />
        </SvgIcon>
      );
    }
    return icon;
  };

  return (
    <Link
      href={props.linkItem.link}
      color="inherit"
      underline="none"
      target="_blank"
      rel="noopener"
      component="a"
    >
      <ListItem button divider>
        <ListItemText primary={props.linkItem.title} />
        <ListItemIcon>{actionLinkIcon(props.linkItem.link)}</ListItemIcon>
      </ListItem>
    </Link>
  );
};

export default CardLink;
