import React, { useState } from 'react';
import { matchSorter } from 'match-sorter';
import {
  InputBase,
  Paper,
  List,
  ListItem,
  ListItemText,
  ListItemAvatar,
  Avatar,
  SvgIcon,
} from '@material-ui/core';
import { makeStyles, alpha } from '@material-ui/core/styles';

import { searchIcon } from '../../assets/svg/SvgIcons';
import { FullList } from '../Demo/FullList';

const useStyles = makeStyles((theme) => ({
  searchContainer: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: alpha(
      theme.palette.mode === 'dark'
        ? theme.palette.common.white
        : theme.palette.common.black,
      0.15
    ),
    '&:hover': {
      backgroundColor: alpha(
        theme.palette.mode === 'dark'
          ? theme.palette.common.white
          : theme.palette.common.black,
        0.25
      ),
    },
    marginLeft: theme.spacing(1),
    width: 'auto',
  },
  searchIcon: {
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputWrapper: {
    color: 'inherit',
  },
  searchInput: {
    padding: theme.spacing(1, 1, 1, 0),
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    width: '12ch',
    '&:focus': {
      width: '24ch',
    },
    position: 'relative',
  },
  searchListContainer: {
    position: 'absolute',
    maxHeight: 600,
    overflow: 'auto',
    backgroundColor: theme.palette.background.paper,
  },
}));

const SearchBar = () => {
  const classes = useStyles();
  const [searchList, setSearchList] = useState(FullList);

  const [searchOptions, setSearchOptions] = useState({
    // isCaseSensitive: false,
    // includeScore: false,
    // shouldSort: true,
    // includeMatches: false,
    // findAllMatches: false,
    // minMatchCharLength: 1,
    // location: 0,
    // threshold: 0.6,
    // distance: 100,
    // useExtendedSearch: false,
    // ignoreLocation: false,
    // ignoreFieldNorm: false,
    keys: [
      'initLink',
      // 'name',
      // 'companyName',
      // 'position',
      // (list) => list.links.map((link) => link.title),
      // (list) => list.links.map((link) => link.link),
      // '[tags]',
      // 'categories',
      // (list) => list.details.map((detail) => detail.title),
      // (list) => list.details.map((detail) => detail.description),
      // 'address',
      'contactPerson.contactName',
      'contactPerson.contactNumber',
      'contactPerson.contactEmail',
    ],
  });

  const [searchKey, setSearchKey] = useState('');
  const [resultList, setResultList] = useState([]);

  const searchKeyHandler = (event) => {
    setSearchKey(event.target.value);
    // const fuse = new Fuse(searchList, searchOptions);
    // const searchResult = fuse.search(searchKey);
    // setResultList(searchResult);
    const searchResult = matchSorter(searchList, searchKey, searchOptions);
    setResultList(searchResult);
  };

  const resetSearchHandler = () => {
    setSearchKey('');
    setResultList([]);
  };

  const searchItemHandler = (event) => {
    event.preventDefault();
  };

  return (
    <>
      <div className={classes.searchContainer}>
        <div className={classes.searchIcon}>
          <SvgIcon>
            <path d={searchIcon.path} />
          </SvgIcon>
        </div>
        <InputBase
          type="text"
          placeholder="Search…"
          classes={{
            root: classes.inputWrapper,
            input: classes.searchInput,
          }}
          inputProps={{ 'aria-label': 'search' }}
          onChange={searchKeyHandler}
          onBlur={resetSearchHandler}
          value={searchKey}
        />
        {resultList.length > 0 ? (
          <Paper className={classes.searchListContainer}>
            <List>
              {resultList.map((resultItemList) => (
                <ListItem
                  button
                  divider
                  key={Math.random()}
                  onMouseDown={searchItemHandler}
                >
                  <ListItemAvatar>
                    <Avatar src={resultItemList.users.image} />
                  </ListItemAvatar>
                  <ListItemText
                    primary={`${resultItemList.contactPerson.contactName}`}
                    secondary={`${resultItemList.contactPerson.contactNumber}`}
                  />
                </ListItem>
              ))}
            </List>
          </Paper>
        ) : null}
      </div>
    </>
  );
};

export default SearchBar;
