import React from 'react';
import { Button, Typography } from '@material-ui/core';
import { Link as RouterLink } from 'react-router-dom';

const Logo = (props) => {
  const { tabsValueHandler, isActiveStyle, routeHistory } = props;
  return (
    <Button
      component={RouterLink}
      to="/"
      style={isActiveStyle(routeHistory, '/')}
      color="inherit"
      onClick={tabsValueHandler}
    >
      <Typography variant="h6">Initcard</Typography>
    </Button>
  );
};

export default Logo;
