import React, { useState } from 'react';
import {
  Grid,
  Tooltip,
  IconButton,
  Avatar,
  ClickAwayListener,
  Popper,
  Zoom,
  Divider,
  Paper,
  useMediaQuery,
} from '@material-ui/core';
import { makeStyles, withStyles, useTheme } from '@material-ui/core/styles';

import { useStore } from '../../hooks/store';
import AccountDropDownOptions from './AccountDropDownOptions';
import AccountInfo from './AccountInfo';
import DrawerContainer from './DrawerContainer';

const useStyles = makeStyles((theme) => ({
  accountDropDownContainer: {
    width: 300,
  },
}));

const styles = (theme) => ({
  moreOptionsContainer: {},
});

const MoreOptionsContainer = withStyles(styles)(
  React.forwardRef((props, ref) => {
    const { classes, children, ...rest } = props;
    return (
      <Paper className={classes.moreOptionsContainer} {...rest}>
        {children}
      </Paper>
    );
  })
);

const AccountDropDown = (props) => {
  const {
    accountItemList,
    toggleDrawer,
    drawerIsOpen,
    tabsValueHandler,
    verticalNavItemList,
    verticalNavItemValue,
    isActive,
    history,
  } = props;
  const theme = useTheme();
  const [state, dispatch] = useStore(true);
  const [accountDropDownIsOpen, setAccountDropDownIsOpen] = useState(false);
  const [accountAnchorEl, setAccountAnchorEl] = useState(null);

  const classes = useStyles();
  const isSmallDevice = useMediaQuery(theme.breakpoints.down('sm'));

  const accountDropDownHandler = (event) => {
    setAccountDropDownIsOpen((prev) => !prev);
    setAccountAnchorEl(accountAnchorEl ? null : event.currentTarget);
  };

  const popperId = props.dropDownIsOpen
    ? 'account-drop-down-popper'
    : undefined;

  return (
    <Grid item>
      <Tooltip TransitionComponent={Zoom} title="Account">
        <IconButton
          edge={false}
          aria-describedby={popperId}
          style={{ padding: 0 }}
          onClick={isSmallDevice ? toggleDrawer : accountDropDownHandler}
          ref={accountAnchorEl}
          disabled={accountDropDownIsOpen}
          size={isSmallDevice ? 'medium' : 'small'}
        >
          <Avatar src={state.isAuthenticated ? state.user.image : null} />
        </IconButton>
      </Tooltip>
      {isSmallDevice ? (
        <DrawerContainer
          accountItemList={accountItemList}
          toggleDrawer={toggleDrawer}
          drawerIsOpen={drawerIsOpen}
          tabsValueHandler={tabsValueHandler}
          verticalNavItemList={verticalNavItemList}
          verticalNavItemValue={verticalNavItemValue}
          isActive={isActive}
          history={history}
        />
      ) : (
        <ClickAwayListener
          mouseEvent="onMouseDown"
          touchEvent="onTouchStart"
          onClickAway={accountDropDownHandler}
        >
          <Popper
            id={popperId}
            open={accountDropDownIsOpen}
            anchorEl={accountAnchorEl}
            popperRef={accountAnchorEl}
            placement="bottom-end"
          >
            <MoreOptionsContainer className={classes.accountDropDownContainer}>
              <AccountInfo direction="row" />
              <Divider />
              <AccountDropDownOptions
                options={accountItemList}
                optionsHandler={accountDropDownHandler}
              />
            </MoreOptionsContainer>
          </Popper>
        </ClickAwayListener>
      )}
    </Grid>
  );
};

export default AccountDropDown;
