import React, { useState } from 'react';
import {
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Typography,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { ExpandMore as ExpandMoreIcon } from '@material-ui/icons';
import { grey } from '@material-ui/core/colors';

const useStyles = makeStyles((theme) => ({
  detailItemContainer: {
    backgroundColor: theme.palette.mode === 'dark' ? grey[800] : grey[100],
  },
}));

const CardDescriptionList = (props) => {
  const [expanded, setExpanded] = useState('');
  const classes = useStyles();

  const detailItemHander = (panel) => (event, newExpanded) => {
    setExpanded(newExpanded ? panel : '');
  };

  return props.details.map((detailItem, index) => (
    <Accordion
      square
      expanded={expanded === detailItem.title}
      onChange={detailItemHander(detailItem.title)}
      className={classes.detailItemContainer}
      key={Math.random()}
      elevation={2}
    >
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls={`panel-${detailItem.title}-content`}
        id={`panel-${detailItem.title}-header`}
      >
        <Typography>{detailItem.title}</Typography>
      </AccordionSummary>
      <AccordionDetails>
        <Typography>{detailItem.description}</Typography>
      </AccordionDetails>
    </Accordion>
  ));
};

export default CardDescriptionList;
